@import "variables"

#title-bar
	margin-top: $header-height
	padding-top: 20px

	h1
		color: $primary-color

	h1, h2
		width: 100vw
		text-align: center

	h2
		font-weight: lighter

#covers
	margin: calc(#{$header-height} + #{$side-margin}) $side-margin
	display: grid
	grid-template-columns: 25vw 25vw 25vw 25vw

	img
		width: 20vw
		filter: drop-shadow(0px 0px 1px rgba(0, 0, 0, .3)) drop-shadow(0px 0px 10px rgba(0, 0, 0, .3))
		transition: transform 200ms ease-in-out
		object-fit: contain
		max-height: 30vh
		cursor: pointer

		&:nth-of-type(1)
			transform: rotate(-30deg) translateX(7vw) translateY(15vh)

			&:hover
				transform: rotate(-30deg) translateX(7vw) translateY(15vh) scale(1.1)


		&:nth-of-type(2)
			transform: rotate(-20deg) translateX(2vw)

			&:hover
				transform: rotate(-20deg) translateX(2vw) scale(1.1)

		&:nth-of-type(3)
			transform: rotate(20deg) translateX(-2vw)

			&:hover
				transform: rotate(20deg) translateX(-2vw) scale(1.1)

		&:nth-of-type(4)
			transform: rotate(30deg) translateX(-7vw) translateY(15vh)

			&:hover
				transform: rotate(30deg) translateX(-7vw) translateY(15vh) scale(1.1)


#info-bar
	text-align: center

	*
		display: none

	.visible
		display: block

	@media (max-width: 1000px)
		margin-top: 15vh

	h3
		width: 100vw
